import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params, Route } from '@angular/router';
import { SharedService } from '../../shared/services/shared.service';
import { RouterModule } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-main-survey',
  templateUrl: './main-survey.component.html',
  styleUrls: ['./main-survey.component.css']
})
export class MainSurveyComponent implements OnInit {

    busy: Promise<any>;

  	public selectedSurvey = [];
  	public questions      = [];
  	public selectedIndex  = 0;
  	public myarray: Array<string> = ['A','B','C']
  	public displayText:any;
    constructor(
    	private _router: Router,
        private toastr: ToastrService,
        private _sharedService: SharedService ) { 
    }

    ngOnInit() {
    	this.getSurvey();
    }

    getSurvey() {

    	this.busy = this._sharedService.getSurvey('mainSurvey').then((res: any) => {
            this.selectedSurvey = res.data.categoryAndQuestions;

	    	this.selectedSurvey.forEach((object)=>{
	    		object.itemsDropped.forEach((obj)=>{
	    			this.questions.push(obj);
	    		})
	    	})
        },(error) => {
            if(error.headers._headers.get('content-type')[0] == "application/json; charset=utf-8") {
              this.toastr.error(error.json().msg);
            } else {
              this.toastr.error('you are not able to login. Please try later.');
            }
        });


    	console.log("this.questions",this.questions)
    }

    next() {
    	if(this.selectedIndex < (this.questions.length-1)) this.selectedIndex++;
    	else this.submitAnswer();
    }

    submitAnswer() {
    	let body = {
    		useId:localStorage.getItem("userID"),
    		surveyType:"mainSurvey",
    		userSurvey:this.questions
    	}
    	this.busy = this._sharedService.submitSurvey(body).then((res: any) => {
            this.toastr.success('Survey Successfully Submited ', '.', { timeOut: 3000, });
            this._router.navigate(['/pages/story']);
        },(error) => {
        	console.log("error in submit answer",error)
            if(error.headers._headers.get('content-type')[0] == "application/json; charset=utf-8") {
              this.toastr.error(error.json().msg);
            } else {
              this.toastr.error('you are not able to login. Please try later.');
            }
        });
    }

}




